<template>
    <div class="fun-fact">
          <em>Quick Bytes:</em>
          <p>text</p>
    </div>
</template>

<script>
export default {
    name: 'FunFacts',
    async mounted() {
        let facts = [
        'My first-ever game was made in <em>notepad</em> with batch.',
        'I started programming when I was <em>seven</em>.',
        'I play the <em>guitar</em>.',
        'My first game engine was <em>Unity</em>.',
        'I am learning <em>React</em>!',
        'I used to make <em>music</em> with GarageBand loops.',
        'I made minecraft <em>animations</em> with Blender.',
        'I\'ve been featured on <em>Squared Media</em> for my animations!',
        'I have remade the <em>same</em> FPS game about <em>24</em> times :p',
        ]
        let i = 0
        for(;;){
            if (i >= facts.length) {i = 0} // keep resetting the loop
            await new Promise(r => setTimeout(r, 8000));

            this.$el.children[1].innerHTML = `<p>${facts[i]}</p>`;
            this.$el.classList.add("visible");
            i++;

            await new Promise(r => setTimeout(r, 15000));
            this.$el.classList.remove("visible")
        }
    }
}
</script>
<style scoped>
/deep/p{margin: 0}
</style>