  methods: {
    debounce(fn, delay) {
      let timeout;
      return function (...args) {
        clearTimeout(timeout);
        timeout = setTimeout(() => fn.apply(this, args), delay);
      };
    },
    setupTimeline() {
      // create an array that automatically goes from 2017
      // (my oldest project) to the current year

      const d = new Date();
      let maxnum = d.getFullYear() - 2000
      let years = []
      let usedYears = []

      for (let i = 17; i <= maxnum; i++) {
        years.push(i)
      }
      years.reverse(); // eg: [25, 24, 23, 22, 21, 20, 19, 18, 17]

      let yearHeads = [] // first element of each of the years (latest project of that year)

      for (let year of years) {
        let head = document.querySelector(`.proj_year_${year}`)
        if (head) {
          yearHeads.push(head); usedYears.push(year)
        } else continue;
      }
      // console.log(yearHeads)
      // console.log(usedYears)
      let topBoundingValues = []
      for (let firstYearElm of yearHeads) {
        topBoundingValues.push(firstYearElm.getBoundingClientRect().top)
      }

      let heightRectValues = []
      for (let value of topBoundingValues) {
        let next = topBoundingValues[topBoundingValues.indexOf(value) + 1]
        if (next) {
          heightRectValues.push(next - value - 48)
        }
      }
      // now begin creating year cards and vertical lines
      let finalOffset = 0
      let yearParent = document.querySelector(".timeline-years")
      yearParent.innerHTML = ""
      for (let i in yearHeads) {
        let head = document.createElement("span");
        let vl = document.createElement("div");
        vl.classList = 'vl';
        finalOffset += 48;

        if (!heightRectValues[i]) {
          let lastHeight = document.querySelector(".project-container").getBoundingClientRect().height - finalOffset
          vl.style.height = `${lastHeight}px`
        } else {
          finalOffset += heightRectValues[i]
        }
        head.innerText = `20${usedYears[i]}`
        vl.style.height = `${heightRectValues[i]}px`
        yearParent.appendChild(head)
        yearParent.appendChild(vl)
        // console

      }
    }
  },
  created() {
    this.debouncedSetupTimeline = this.debounce(this.setupTimeline, 100);
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.debouncedSetupTimeline);
  },
  mounted() {
    ////////////////////////////  
    // NAVIGATION
    ////////////////////////////
    let navParent = document.querySelector(".navigation")

    function clearChildrenStyles(parent) {
      for (let child of parent.children) {
        child.classList = ''
      }
    }

    let sectionBounds = []
    this.$nextTick(() => {
      setTimeout(() => {

        let sections = document.querySelectorAll(".section")

        for (let section of sections) {
          sectionBounds.push(section.getBoundingClientRect().y + window.scrollY)
        }
        sectionBounds.shift();
      }
        , 100);
    });

    window.addEventListener('scroll', () => {
      let scrollValue = window.scrollY;

      for (let i in sectionBounds) {
        if (scrollValue < sectionBounds[i]) {
          clearChildrenStyles(navParent)
          // console.log("active is" + i)
          navParent.children[i].classList = "nav-active"
          break
        }
      }
    })
    
    for (let i = 0; i < navParent.childElementCount; i++) {
      navParent.children[i].addEventListener('click', () => {
        window.scrollTo(0, sectionBounds[i - 1])
      })
    }
    // for (let navItem of navParent.children) {
    //   navItem.addEventListener('click', () => {
    //     window.scrollTo(0, sectionBounds[index])
    //   })
    // }
    ////////////////////////////
    /// Timeline js
    ////////////////////////////
    this.$nextTick(() => {
      setTimeout(() => this.setupTimeline(), 100);
    });
    window.addEventListener("resize", this.debouncedSetupTimeline);

  }
}
